*{
    box-sizing: border-box;
    margin: 0;
}

.content{
    display: flex;
    flex-direction: column;
    background: url('../img/prague.jpg') center fixed;
    background-size: cover;
    font-family: 'Roboto Condensed', sans-serif;
    width: 100%;
    height: 100%;

    @media screen and (max-width: 300px){
        background: url('../img/prague.jpg');
     }
}

.welcome{
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    height: 10em;

    @media screen and (max-width: 800px){
        display: flex;
        flex-direction: column;
       /*  justify-content: center; */
        align-items: center;
        height: 100%;
     }
    /* background: white; */
       
    a{  display:flex;
        background-color:rgba(224,171,119, 0.5);
        height: 3rem;
        padding: 1rem;
        padding-right: 2rem;
        width:16%;
        color: white;
        text-decoration: none;
        box-shadow: 5px 3px 5px -3px rgba(145,145,145,1);

        @media screen and (max-width: 800px)
             {
            text-align: center;
            align-items: center;
            justify-content: center;
            align-content: center;
            font-size: 2rem;
            width:100%;
            
            }
        
        &.home{
     /*        position:fixed;
            width:16%;
            background-color: rgba(222,101,74, 0.7);    */        
        }
    }
  
}

.banner{
    position: relative;
    display:flex;
    background-color: rgba(145,145,145,0.4);
    background-size: cover;
    width: 100%;
    height: 20em;

    @media screen and (max-width: 800px){
        display: block;
        text-align: center;
        font-size: 1.1em; 
            } 

    .titulek{
        position:absolute;
        left: 11rem;
        top: 3rem;
        color: rgb(243, 235, 235);
        font-style: italic;
        font-size: 1.5em;
        line-height: 4rem;

        @media screen and (max-width: 1022px){
            font-size: /* 1.3em */ 2.2vw;
            }
                  
        @media screen and (max-width: 910px){
                font-size: /* 1.1em */2.2vw;
                }
        
        @media screen and (max-width: 800px){
            position: relative;
            left: 1.5rem;
            text-align: center;
                 } 

        @media screen and (max-width: 400px){
            position: relative;
            margin: 0 auto;
            left: auto;
            font-size: 1.1em;
                } 
     

        img{
            position:absolute;
            top:5rem;
            bottom: 1px;
            margin-left: 94%;

         @media screen and (max-width: 800px){
            display: none;
            } 
        } 
    }
}

